.container {
  width: 100%;
  max-width: 1440px;
  padding: 0 35px;
  margin: 0 auto;
}
@media (min-width: 1440px) {
  .container {
    padding: 0 65px;
  }
}

.main-header {
  width: 100%;
  padding: 84px 0 34px 0;
}
@media (min-width: 768px) {
  .main-header {
    padding: 50px 0;
  }
}

.main-header__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.main-header__logo {
  color: var(--color-logo);
}

.main-header__copy-message {
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  padding: 11px;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  color: #000000;
  background-image: linear-gradient(180deg, #e9efff 0%, #ffffff 100%);
  border-radius: 46px;
  transform: translate(-50%, -200px);
  transition: all 0.3s ease-out;
}
@media (min-width: 768px) {
  .main-header__copy-message--active {
    min-width: 200px;
    transform: translate(-50%, 0);
  }
}
@media (min-width: 1440px) {
  .main-header__copy-message--active {
    min-width: 292px;
  }
}

.main-header__nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-header__nav-item {
  position: relative;
}
@media (max-width: 767px) {
  .main-header__nav-item:first-child {
    display: none;
  }
}
.main-header__nav-item span {
  display: block;
  max-width: 250px;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-text);
}
.main-header__nav-item .main-header__tooltip {
  position: absolute;
  bottom: -40px;
  left: 50%;
  display: none;
  min-width: 70px;
  padding: 3px;
  font-size: 10px;
  text-align: center;
  background-color: #e9efff;
  color: #000000;
  border-radius: 5px;
  transform: translateX(-50%);
}
.main-header__nav-item .main-header__tooltip::after {
  content: "";
  position: absolute;
  top: -10px;
  left: 50%;
  width: 15px;
  height: 15px;
  background-image: url("../img/sprite.svg#icon-tooltip");
  transform: translateX(-50%);
}
@media (min-width: 1440px) {
  .main-header__nav-item .main-header__tooltip {
    min-width: 106px;
  }
}

.main-header__nav-button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s ease-out;
}
.main-header__nav-button:hover {
  opacity: 0.8;
}
.main-header__nav-button:active {
  opacity: 0.6;
}

.main-header__nav-button:hover + .main-header__tooltip {
  display: block;
}

.main-achievements {
  position: relative;
  display: grid;
  padding: 56px 0 0 0;
}
.main-achievements::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: var(--bg-main-mobile-width);
  height: var(--bg-main-mobile-height);
  background-image: var(--bg-main-mobile);
}
.main-achievements p {
  max-width: 310px;
  margin: 0 0 11px 0;
}
@media (min-width: 768px) {
  .main-achievements p {
    max-width: 500px;
    margin: 0 0 7px 0;
  }
}
@media (min-width: 1440px) {
  .main-achievements {
    padding: 82px 0 0 0;
  }
  .main-achievements::after {
    top: var(--bg-main-desktop-top-position);
    width: var(--bg-main-desktop-top-width);
    height: var(--bg-main-desktop-top-height);
    background-image: var(--bg-main-desktop-top);
  }
  .main-achievements::before {
    content: "";
    position: absolute;
    bottom: var(--bg-main-desktop-middle-position);
    right: 0;
    width: var(--bg-main-desktop-middle-width);
    height: var(--bg-main-desktop-middle-height);
    background-image: var(--bg-main-desktop-middle);
  }
}

.main-achievements__slider.swiper {
  position: relative;
  max-width: 100%;
  min-width: 0;
  margin: 0 0 72px 0;
  padding: 0 0 0 35px;
}
@media (min-width: 1440px) {
  .main-achievements__slider.swiper {
    margin: 0 auto 60px auto;
    padding: 0 0 0 65px;
  }
}
.main-achievements__slider.swiper .swiper-wrapper {
  max-width: 100%;
}

.main-achievements__slide.swiper-slide {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 270px;
  max-height: 400px;
  width: 100%;
  text-align: center;
}
.main-achievements__slide.swiper-slide p {
  margin: 0;
  font-weight: 600;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
}
.main-achievements__slide.swiper-slide p:first-of-type {
  text-transform: uppercase;
}
.main-achievements__slide.swiper-slide p span {
  font-weight: 500;
  /* stylelint-disable-next-line */
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.main-achievements__slide.swiper-slide h3 {
  margin: 0 0 22px 0;
  font-weight: 600;
  font-size: 23px;
  line-height: 30px;
  color: #000000;
  text-align: center;
}
@media (min-width: 768px) {
  .main-achievements__slide.swiper-slide h3 {
    font-size: 27px;
    line-height: 34px;
  }
}
.main-achievements__slide.swiper-slide img:first-of-type {
  text-align: center;
}
.main-achievements__slide.swiper-slide img:last-of-type {
  position: absolute;
  left: 4px;
  width: calc(100% - 7px);
  bottom: 3px;
}
.main-achievements__slide.swiper-slide:nth-child(even) {
  text-align: left;
}
.main-achievements__slide.swiper-slide:nth-child(even) p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-transform: unset;
}
.main-achievements__slide.swiper-slide:nth-child(even) a {
  color: #477bf5;
}
@media (max-width: 767px) {
  .main-achievements__slide.swiper-slide:nth-child(even) {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-achievements__slide.swiper-slide {
    max-width: 323px;
    max-height: 476px;
  }
  .main-achievements__slide.swiper-slide p {
    font-size: 28px;
  }
}

.main-achievements__slide-wrapper {
  width: calc(100% - 8px);
  height: calc(100% - 8px);
  background-image: linear-gradient(180deg, #f4f7ff 0%, #ffffff 100%);
  border-radius: 32px;
}

.main-achievements__slide-box {
  min-height: 400px;
  padding: 34px 18px;
}
@media (min-width: 768px) {
  .main-achievements__slide-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 480px;
    padding: 40px 26px;
  }
}

.main-achievements__slide-header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 0 22px 0;
}

.main-achievements__buttons {
  display: flex;
  justify-content: space-between;
  min-width: 75px;
  margin: auto 0 0 0;
}

.main-achievements__button {
  height: 20px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.main-achievements__button--next {
  transform: rotate(180deg);
}

.main-achievements__next-button {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-self: center;
  height: 38px;
  width: 76px;
  margin: 0 0 36px 0;
  border: none;
  border-radius: 50% 50% 0 0/100% 100% 0 0;
  overflow: hidden;
  background-color: transparent;
  background-image: var(--bg-item);
}
@media (min-width: 768px) {
  .main-achievements__next-button {
    width: 132px;
    height: 66px;
    margin: 0 0 85px 0;
  }
}
@media (min-width: 768px) {
  .main-achievements__next-button svg {
    width: 54px;
    height: 17px;
  }
}

.achievements {
  position: relative;
  padding-bottom: 40px;
}
@media (min-width: 1440px) {
  .achievements {
    padding-bottom: var(--bg-main-desktop-bottom-indent);
  }
  .achievements::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: var(--bg-main-desktop-bottom-width);
    height: var(--bg-main-desktop-bottom-height);
    background-image: var(--bg-main-desktop-bottom);
  }
}
.achievements h2 {
  margin: 0 0 23px 0;
}
@media (min-width: 768px) {
  .achievements h2 {
    margin: 0 0 60px 0;
  }
}
.achievements p {
  line-height: 15px;
}

.achievements__wrapper {
  display: flex;
  flex-direction: column;
}

.achievements__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px 30px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .achievements__list {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px 40px;
  }
}
@media (min-width: 1440px) {
  .achievements__list {
    grid-template-columns: repeat(4, 1fr);
    gap: 50px 64px;
  }
}

.achievements__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 145px;
  min-height: 145px;
  padding: 28px 10px 5px 10px;
  background-image: var(--bg-item);
  border-radius: 24px;
}
@media (min-width: 768px) {
  .achievements__item {
    width: 180px;
    min-height: 180px;
    padding: 46px 15px 15px 15px;
  }
}
@media (min-width: 1440px) {
  .achievements__item {
    width: 280px;
    min-height: 280px;
    padding: 76px 20px 18px 20px;
  }
}
.achievements__item p {
  max-width: 120px;
  margin: auto 0 0 0;
  text-align: center;
}
@media (min-width: 768px) {
  .achievements__item p {
    max-width: unset;
  }
}
@media (min-width: 768px) {
  .achievements__item img {
    transform: scale(1.4);
  }
}
@media (min-width: 1440px) {
  .achievements__item img {
    transform: scale(1.84);
  }
}

